.particles {
    position: fixed;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    z-index: -1;
}

.bg-dwyl-teal {
    font-family: 'Roboto', sans-serif;
    font-size: 1rem;
    background-color: #F0C48F;
}

h2 {
    font-family: 'Montserrat', sans-serif;
    font-size: 5rem;
    text-align: center;
    color: white;
}

h3 {
    font-family: 'Montserrat', sans-serif;
    font-size: 2rem;
    text-align: center;
    color: white;
}

p {
    font-family: 'Roboto', sans-serif;
    font-size: 1.2rem;
    text-align: center;
    color: white;
}

